body {
  background-image: url('../assets/jesse-schoff-Jur3IpOce6E-unsplash.jpg');
  background-position: top;
  background-repeat: no-repeat;
  background-color: black;
  background-size: 100%;
}

.resume-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: auto;
  width: 50%;
}

.resume-content {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 30px 20px 0;
  width: 100%;
  border-bottom: 1px solid #cecece;
  font-family: Roboto, sans-serif;
  font-weight: 100;
  font-size: 15px;
  color: #333333;
}